<script>
var _ = require("lodash");
export default {
  name: "Food",
  props: {},
  data() {
    return {
      foods: [],
      newFood: "",
      f: [],
      cache: [],
      isCheckAll: false,
      selectedCheck: "",
    };
  },
  methods: {
    addFood: function() {
      this.foods.push({
        name: this.newFood,
        pending: false,
      });
      //console.log(this.foods);
      this.newFood = "";
    },
    doEdit: function(f) {
      var index = _.indexOf(this.foods, this.cache);
      this.foods.splice(index, 1, f);
    },
    editFood: function(f) {
      this.f = _.cloneDeep(f);
      this.cache = f;
    },
    cancel: function() {
      this.newFood = "";
    },
    checkAll: function() {
      this.isCheckAll = !this.isCheckAll;
      this.f = [];

      if (this.isCheckAll) {
        for (var key in this.foods) this.f.push(this.foods[key]);
      }
    },
    updateCheckall: function() {
      if (this.f.length == this.foods.length) {
        this.isCheckAll = true;
      } else {
        this.isCheckAll = false;
      }
    },
  },
};
</script>

